.badge-pill {
    border-radius: 50rem;
}

@each $modifier, $value in $theme-colors {
    .badge-#{$modifier} {
        background-color: $value !important;
        @if $modifier == 'light' {
            color: $primary;
        }

        @if $modifier == 'white' {
            color: $primary;
        }

        @if $modifier == 'secondary' {
            color: $primary;
        }
    }
}