@each $modifier, $value in $spacers {
    .pt-#{$modifier}, .py-#{$modifier}, .p-#{$modifier} {
        padding-top: $value !important;
    }

    .pb-#{$modifier}, .py-#{$modifier}, .p-#{$modifier} {
        padding-bottom: $value !important;
    }

    .ps-#{$modifier}, .px-#{$modifier}, .p-#{$modifier} {
        padding-left: $value !important;
    }

    .pe-#{$modifier}, .px-#{$modifier}, .p-#{$modifier} {
        padding-right: $value !important;
    }

    .mt-#{$modifier}, .my-#{$modifier}, .m-#{$modifier} {
        margin-top: $value !important;
    }

    .mb-#{$modifier}, .my-#{$modifier}, .m-#{$modifier} {
        margin-bottom: $value !important;
    }

    .ms-#{$modifier}, .mx-#{$modifier}, .m-#{$modifier} {
        margin-left: $value !important;
    }

    .me-#{$modifier}, .mx-#{$modifier}, .m-#{$modifier} {
        margin-right: $value !important;
    }
}