// Fonts
$ff-display:        'Timmons NY';
$ff-header:         'Plus Jakarta Sans', 'Calibre';
$ff-body:           'Plus Jakarta Sans', 'Calibre';  
$ff-mono:           'Pitch Sans';

$font-family: (
    "display":  $ff-display,
    "header":   $ff-header,
    "body":     $ff-body,
    "mono":     $ff-mono,
) !default;

//Font Sizes
$fs-base:       1rem;
$fs-small:      $fs-base / 1.250;
$fs-1:          $fs-base;
$fs-2:          $fs-1 * 1.250;
$fs-3:          $fs-2 * 1.250;
$fs-4:          $fs-3 * 1.250;
$fs-5:          $fs-4 * 1.250;
$fs-6:          $fs-5 * 1.250;
$fs-7:          $fs-6 * 1.250;
$fs-8:          $fs-7 * 1.250;
$fs-9:          $fs-8 * 1.250;
$fs-10:         $fs-9 * 1.250;

$font-size: (
    "1":        $fs-1,
    "2":        $fs-2,
    "3":        $fs-3,
    "4":        $fs-4,
    "5":        $fs-5,
    "6":        $fs-6,
    "7":        $fs-7,
    "8":        $fs-8,
    "9":        $fs-9,
    "10":       $fs-10,
) !default;

$font-weights: (100, 200, 300, 400, 500, 600, 700, 800, 900, light, normal, bold)!default;


//Spacers
$spacer-1:          $fs-base;
$spacer-2:          $spacer-1 * 1.250;
$spacer-3:          $spacer-2 * 1.250;
$spacer-4:          $spacer-3 * 1.250;
$spacer-5:          $spacer-4 * 1.250;
$spacer-6:          $spacer-5 * 1.250;
$spacer-7:          $spacer-6 * 1.250;
$spacer-8:          $spacer-7 * 1.250;
$spacer-9:          $spacer-8 * 1.250;
$spacer-10:         $spacer-9 * 1.250;

$spacers: (
    "1":        $spacer-1,
    "2":        $spacer-2,
    "3":        $spacer-3,
    "4":        $spacer-4,
    "5":        $spacer-5,
    "6":        $spacer-6,
    "7":        $spacer-7,
    "8":        $spacer-8,
    "9":        $spacer-9,
    "10":       $spacer-10,
) !default;


// Colors
$primary:      #172122;
$secondary:    #f68e1e;
$dark:         #2B3233;
$light:        #d4d9c3;
$white:        #ffffff;
$black:        #000000;

$theme-colors: (
    "primary":      $primary,
    "secondary":    $secondary,
    "dark":         $dark,
    "light":        $light,
    "white":        $white,
    "black":        $black,
) !default;


// Breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);

$breakpoint-xs:         0;
$breakpoint-sm:         576px;
$breakpoint-md:         768px;
$breakpoint-lg:         992px;
$breakpoint-xl:         1200px;
$breakpoint-xxl:        1400px;