body {
    font-family: $ff-body !important;
    --webkit-font-smoothing: antialiased !important;
    --mox-osx-font-smoothing: grayscale !important;
}

body > a, p > a, a {
    color: inherit;
    font-weight: bold;
}

p > em, body > em, em {
    font-style: italic;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
}

@each $modifier, $value in $font-family {
    .ff-#{$modifier} {
        font-family: $value !important;
        @if $modifier == "display" {
            line-height: .8 !important;
        }
        @if $modifier == "header" {
            font-weight: normal !important;
        }
    }
}

@each $modifier, $value in $font-size {
    .fs-#{$modifier} {
        font-size: $value !important;
    }
}

@each $modifier, $value in $theme-colors {
    .fc-#{$modifier} {
        color: $value !important;
    }
}

@each $modifier in $font-weights {
    .fw-#{$modifier} {
        font-weight: $modifier !important;
    }
}

.fluid-display {
    font-size: clamp(15vw, 10rem, 7rem);
}

.boxed-lead {
    display: inline-block;
    padding: $spacer-2;
    border: 2px solid $primary;
    letter-spacing: 2px;
    font-size: $fs-small;
    text-transform: uppercase;
}

@each $modifier, $value in $theme-colors {
    .fc-#{$modifier}, .text-#{$modifier} {
        
        &.boxed-lead {
            border: 2px solid $value;
        }

        .boxed-lead {
            border: 2px solid $value;
        }


    }
}

.dropcap {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-top: $spacer-1 / 2;
    margin-right: $spacer-1 / 2;
    $margin-bottom: $spacer-1 / 2;
    line-height: 0;
    height: 60px;
    width: 60px;
    font-size: $fs-4;
    font-family: $ff-display;
    float: left;
    border: 2px solid $primary;
    background-color: $primary;
}

@each $modifier, $value in $theme-colors {
    .fc-#{$modifier}, .text-#{$modifier} {
        
        &.dropcap {
            border: 2px solid $value !important;
            background-color: $value !important;
            @if $modifier != 'dark' {
                color: $primary !important;
            }
            @if $modifier != 'black' {
                color: $primary !important;
            }
        }

        .dropcap {
            border: 2px solid $value !important;
            background-color: $value !important;
            @if $modifier != 'dark' {
                color: $primary !important;
            }
            @if $modifier != 'black' {
                color: $primary !important;
            }
        }

        > .dropcap {
            border: 2px solid $value !important;
            background-color: $value !important;
            @if $modifier != 'dark' {
                color: $primary !important;
            }
            @if $modifier != 'black' {
                color: $primary !important;
            }
        }


    }
}

.icon-list {
    li {
        margin-left: $spacer-3;
        margin-bottom: $spacer-1 * .25;
        position: relative;
        &:before {
            content: 'â€”';
            position: absolute;
            left: -$spacer-3;
        }
    }
}

.text- {
    &left {
        text-align: left;
    }
    &center {
        text-align: center;
    }
    &right {
        text-align: right;
    }
}