.index {
    footer {
        display: none;
    }
}

.homepage-body {
    position: relative;

    .col-md-6.left-content  {
        min-height: 100vh;
        height: 100%;
        padding-top: $spacer-3;
        padding-bottom: $spacer-3;
    }

    .featured-image {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        @media screen and (max-width: $breakpoint-md) {
            position: relative;
            display: none;
            height: 0;
            width: 0;
        }

    }

}